import React, { useContext, useState, useEffect } from 'react';
import { Context } from '../context';

const faces = [
  'https://cdn.discordapp.com/attachments/468501401350176798/873613804221652992/x1.png',
  'https://cdn.discordapp.com/attachments/468501401350176798/873613806738214942/x2.png',
  'https://cdn.discordapp.com/attachments/468501401350176798/873613800992026644/x3.png',
  'https://cdn.discordapp.com/attachments/468501401350176798/873613802694909962/x4.png'
]

const fists = [
  'https://cdn.discordapp.com/attachments/468501401350176798/873616943406518272/fist1.png',
  'https://cdn.discordapp.com/attachments/468501401350176798/873616947063971910/fist2.png',
  'https://cdn.discordapp.com/attachments/468501401350176798/873616941493915648/fist3.png',
  'https://cdn.discordapp.com/attachments/468501401350176798/873616942240497674/fist4.png',
]

export default function Character(props) {
  const isLeftFistActive = useContext(Context).isLeftFistActive
  const isRightFistActive = useContext(Context).isRightFistActive
  const isDedHealing = useContext(Context).isDedHealing

  const [healFace, toggleHealFace] = useState(false)

  useEffect(() => {
    if (isDedHealing) {
      const idx = setInterval(() => {
        toggleHealFace(!healFace)
      }, 500);

      return () => clearInterval(idx)
    }
  }, [isDedHealing, healFace])

  return (
    <div className='character'>
      {/* face */}
      <img src={
        isDedHealing ?
          healFace ? faces[2] : faces[3]
          :
          isLeftFistActive || isRightFistActive ? faces[1] : faces[0]
      } className='character_head' />

      {/* body */}
      <img src={props.bodyImg} className='character_body' />

      {/* fists */}
      <div className='character_fists'>
        <img src={isLeftFistActive ? fists[3] : fists[2]} className={isLeftFistActive ? 'character_fist left_fist_active' : 'character_fist '} />
        <img src={isRightFistActive ? fists[1] : fists[0]} className={isRightFistActive ? 'character_fist right_fist_active' : 'character_fist '} />
      </div>
    </div>
  )
}