import React, { useState, useEffect } from 'react';
import { Context } from './context';
import { Howl } from 'howler';
import './App.css'

import Buttons from './components/Buttons'
import Character from './components/Character';
import Display from './components/Display';

const sounds = {
    punch: 'https://cdn.discordapp.com/attachments/468501401350176798/873888501924528148/punch.mp3'
}

const themes = [
    {
        title: "Cyberpunk",
        background: "https://cdn.discordapp.com/attachments/468501401350176798/873565567934660648/bg.jpg",
        bodyImg: "https://cdn.discordapp.com/attachments/468501401350176798/873925969134616646/body.png"
    },
    {
        title: "Apex",
        background: "https://cdn.discordapp.com/attachments/468501401350176798/873923301603430450/apex-1.jpg",
        bodyImg: "https://cdn.discordapp.com/attachments/468501401350176798/873992410508718130/crypto.png"
    },
]

const App = () => {
    const initialState = localStorage.getItem('ded') ? JSON.parse(localStorage.getItem('ded')) : { hp: 100, lvl: 0 }

    const [ded, setDed] = useState(initialState)
    const [isDedHealing, setHealing] = useState(false)
    const [isButtonDisabled, setBtnDisabled] = useState(false)
    const [isLeftFistActive, setLeftFistActive] = useState(false)
    const [isRightFistActive, setRightFistActive] = useState(false)

    const saveDed = (ded) => localStorage.setItem('ded', JSON.stringify(ded))

    // theme
    const [bodyImg, setBodyImg] = useState(themes[0].bodyImg)

    const setTheme = (theme) => {
        document.querySelector('body').style.backgroundImage = "url('" + theme.background + "')";
        setBodyImg(theme.bodyImg)
    }

    // healing
    useEffect(() => {
        if (isDedHealing) {
            setBtnDisabled(true)
            const id = setInterval(() => {
                let newDed = { lvl: ded.lvl, hp: ded.hp + 10 }
                setDed(ded => newDed);
            }, 500);

            if (ded.hp > 99) {
                let newDed = { lvl: ded.lvl + 1, hp: ded.hp = 100 }
                setDed(ded => newDed);
                setHealing(false)
                setBtnDisabled(false)
                saveDed(newDed)
            }

            return () => clearInterval(id)
        }
    }, [isDedHealing, ded, isButtonDisabled, isLeftFistActive, isRightFistActive])

    // select theme
    useEffect(() => {
        setTheme(themes[0])
    }, [])

    const playSound = (src) => {
        const sound = new Howl({
            src: src,
            html5: true,
            volume: 0.1,
        });
        sound.play();
    }

    // attack
    const attackDelay = 50;

    const hit = (side) => {
        playSound(sounds.punch)
        let damage = (9 - ded.lvl > 0) ? 10 - ded.lvl : 1
        setBtnDisabled(true)
        let newDed = { ...ded, hp: ded.hp - damage }
        setDed(newDed)
        saveDed(newDed)

        if (side == 'left') {
            setLeftFistActive(true)
        } else {
            setRightFistActive(true)
        }

        if (ded.hp < 11) {
            setHealing(true)
        }

        setTimeout(() => {
            setRightFistActive(false)
            setLeftFistActive(false)
            setBtnDisabled(false)
        }, attackDelay);
    }

    return (
        <main>
            <div className='links'>
                {themes.map(item => <button onClick={() => setTheme(item)}>{item.title}</button>)}
            </div>

            <Context.Provider value={{ ded, isButtonDisabled, hit, isLeftFistActive, isRightFistActive, isDedHealing }}>
                <div className='homePage'>
                    <Display />
                    <Buttons />
                    <Character bodyImg={bodyImg} />
                </div>
            </Context.Provider>
        </main >
    )
}

export default App