import React, { useContext } from 'react';
import { Context } from '../context';
import './Buttons.css';

export default function Buttons(props) {

  const isButtonDisabled = useContext(Context).isButtonDisabled
  const hit = useContext(Context).hit

  return (
    <div className='hits_buttons'>
      <button className="cybr-btn" disabled={isButtonDisabled} onClick={() => {
        hit('left')
      }}>
        left<span aria-hidden>hit</span>
        <span aria-hidden className="cybr-btn__glitch">left</span>
        <span aria-hidden className="cybr-btn__tag">hit</span>
      </button>

      <button className="cybr-btn" disabled={isButtonDisabled} onClick={() => {
        hit('right')
      }}>
        right<span aria-hidden>_</span>
        <span aria-hidden className="cybr-btn__glitch">right</span>
        <span aria-hidden className="cybr-btn__tag">hit</span>
      </button>
    </div>
  )
}
