import React, { useContext } from 'react';
import { Context } from '../context';

let armors = [
  'https://cdn.discordapp.com/attachments/468501401350176798/873881354759180299/armor0.png',
  'https://cdn.discordapp.com/attachments/468501401350176798/873881356361408512/armor1.png',
  'https://cdn.discordapp.com/attachments/468501401350176798/873881357451935806/armor2.png',
  'https://cdn.discordapp.com/attachments/468501401350176798/873881360199188490/armor3.png'
]

let heads = [
  'https://cdn.discordapp.com/attachments/468501401350176798/873881112471044136/head0.png',
  'https://cdn.discordapp.com/attachments/468501401350176798/873881114241032232/head1.png',
  'https://cdn.discordapp.com/attachments/468501401350176798/873881115247652864/head2.png',
  'https://cdn.discordapp.com/attachments/468501401350176798/873881121945972746/head3.png'
]

export default function Display() {
  const ded = useContext(Context).ded
  const hpBarWidth = 240 / 100 * ded.hp + 'px'
  const displayImg = "https://cdn.discordapp.com/attachments/468501401350176798/873990851309752320/hp.png"

  const style = {
    position: 'absolute',
    height: '30px',
    width: hpBarWidth,
    marginLeft: '132px',
    marginTop: '-50px',
    backgroundColor: 'white',
    zIndex: '-1'
  }

  return (
    <div className='display'>
      <img src={ded.lvl > 2 ? armors[3] : armors[ded.lvl]} className='armor' />
      <img src={ded.lvl > 6 ? heads[3] : ded.lvl - 3 < 0 ? heads[0] : heads[ded.lvl - 3]} className='helmet' />
      <img src={displayImg} className='displayImg' />
      <p className='hp'>{ded.hp}</p>
      <p className='lvl'> level : {ded.lvl + 1}</p>
      <div style={style}></div>
    </div>
  )
}